<template><section><h2 id="事件循环的一点疑惑">事件循环的一点疑惑</h2>
<div class="aside-menu">
		<div class="aside-menu-tit" v-toggle="">menu</div>
		<div class="aside-menu-con"><a href="#关于 promise 有话说" class="level-3 aside-menu-item">关于 promise 有话说</a><a href="#分析" class="level-4 aside-menu-item">分析</a><a href="#总结" class="level-4 aside-menu-item">总结</a><a href="#关于 macrotask & microtask" class="level-3 aside-menu-item">关于 macrotask & microtask</a><a href="#一次事件循环只执行一个 macrotask" class="level-4 aside-menu-item">一次事件循环只执行一个 macrotask</a><a href="#一次事件循环会执行完所有微队列" class="level-4 aside-menu-item">一次事件循环会执行完所有微队列</a></div>
	</div><b class="update-time">{{1542638521139 | formatTime}}</b><b class="type">其他</b>
<b class="kw">promise</b> <b class="kw">事件循环</b> <b class="kw">宏队列与微队列</b>
<blockquote>
<p>我们知道 js 是单线程执行的，但是借助宿主，可以获得异步的能力，在浏览器中依赖 webAPI，在 node 中依赖 libuv
但是这还不够。我们还需要区分 宏队列（macrotask）和微队列（microtask）。我们知道是先执行微队列再执行宏队列</p>
</blockquote>
<h3 id="关于 promise 有话说">关于 promise 有话说</h3>
<p>先看题</p>
<details>
  <summary>js</summary>
<pre v-pre=""><code v-pre="" class="language-js"><span class="hljs-keyword">new</span> <span class="hljs-built_in">Promise</span>(<span class="hljs-function"><span class="hljs-params">resolve</span> =&gt;</span> {
  <span class="hljs-built_in">console</span>.log(<span class="hljs-string">'promise1'</span>)
  resolve(<span class="hljs-number">1</span>)
}).then(<span class="hljs-function"><span class="hljs-params">res</span> =&gt;</span> {
  <span class="hljs-built_in">console</span>.log(<span class="hljs-string">'promise1'</span>, ++res)
  performance.now()
  <span class="hljs-keyword">return</span> res
}).then(<span class="hljs-function"><span class="hljs-params">res</span> =&gt;</span> {
  <span class="hljs-built_in">console</span>.log(<span class="hljs-string">'promise1'</span>, ++res)
  <span class="hljs-keyword">return</span> <span class="hljs-built_in">Promise</span>.resolve(res) <span class="hljs-comment">// 注意这里不一样</span>
}).then(<span class="hljs-function"><span class="hljs-params">res</span> =&gt;</span> {
  <span class="hljs-built_in">console</span>.log(<span class="hljs-string">'promise1'</span>, ++res)
  <span class="hljs-keyword">return</span> res
}).then(<span class="hljs-function"><span class="hljs-params">res</span> =&gt;</span> {
  <span class="hljs-built_in">console</span>.log(<span class="hljs-string">'promise1'</span>, ++res)
  <span class="hljs-keyword">return</span> res
}).then(<span class="hljs-function"><span class="hljs-params">res</span> =&gt;</span> {
  <span class="hljs-built_in">console</span>.log(<span class="hljs-string">'promise1'</span>, ++res)
  <span class="hljs-keyword">return</span> res
})

<span class="hljs-built_in">console</span>.log(<span class="hljs-string">'normal'</span>)

<span class="hljs-keyword">new</span> <span class="hljs-built_in">Promise</span>(<span class="hljs-function"><span class="hljs-params">resolve</span> =&gt;</span> {
  <span class="hljs-built_in">console</span>.log(<span class="hljs-string">'promise2'</span>)
  resolve(<span class="hljs-number">1</span>)
}).then(<span class="hljs-function"><span class="hljs-params">res</span> =&gt;</span> {
  <span class="hljs-built_in">console</span>.log(<span class="hljs-string">'promise2'</span>, ++res)
  <span class="hljs-keyword">return</span> res
}).then(<span class="hljs-function"><span class="hljs-params">res</span> =&gt;</span> {
  <span class="hljs-built_in">console</span>.log(<span class="hljs-string">'promise2'</span>, ++res)
  <span class="hljs-keyword">return</span> res
}).then(<span class="hljs-function"><span class="hljs-params">res</span> =&gt;</span> {
  <span class="hljs-built_in">console</span>.log(<span class="hljs-string">'promise2'</span>, ++res)
  <span class="hljs-keyword">return</span> res
}).then(<span class="hljs-function"><span class="hljs-params">res</span> =&gt;</span> {
  <span class="hljs-built_in">console</span>.log(<span class="hljs-string">'promise2'</span>, ++res)
  <span class="hljs-keyword">return</span> res
}).then(<span class="hljs-function"><span class="hljs-params">res</span> =&gt;</span> {
  <span class="hljs-built_in">console</span>.log(<span class="hljs-string">'promise2'</span>, ++res)
  <span class="hljs-keyword">return</span> res
})
</code></pre>
</details>
<p>咋一看，你的答案可能是这样：</p>
<pre v-pre=""><code v-pre="" class="language-js"><span class="hljs-comment">// 一开始以为的：    实际是这样：</span>
<span class="hljs-comment">// promise1        嗯，你是对的</span>
<span class="hljs-comment">// normal          嗯，你是对的</span>
<span class="hljs-comment">// promise2        嗯，你是对的</span>
<span class="hljs-comment">// promise1 2      嗯，你是对的</span>
<span class="hljs-comment">// promise2 2      嗯，你是对的</span>
<span class="hljs-comment">// promise1 3      嗯，你是对的</span>
<span class="hljs-comment">// promise2 3      嗯，你是对的</span>
<span class="hljs-comment">// promise2 4      嗯，你是对的</span>
<span class="hljs-comment">// promise1 4      promise2 5</span>
<span class="hljs-comment">// promise2 5      promise1 4</span>
<span class="hljs-comment">// promise1 5      promise2 6</span>
<span class="hljs-comment">// promise2 6      promise1 5</span>
<span class="hljs-comment">// promise1 6      嗯，你是对的</span>
</code></pre>
<h4 id="分析">分析</h4>
<p>如果知道事件循环，前面到 <code v-pre="">Promise.resolve(res)</code> 没什么可说的。关键是后面发生了什么？为什么慢两个拍？</p>
<blockquote>
<p>我们知道 promise 在 resolver 和 rejecter 可以返回 promise，并且这个 promise 的返回将传递给后续的 resolver 或 rejecter
而 promise 本身在 then cache 也会返回一个 promise，这也是为什么可以被链式调用，而其状态取决于当前 状态，这也是为什么有值的穿透这个现象了
但是</p>
</blockquote>
<p>我们现在用两格短线表示 then 的时间，上面是 promise1 下面是 promise2</p>
<pre v-pre=""><code v-pre="" class="language-sh">2  3     4  5  6
|__|__ __|__|__|

2  3  4  5  6
|__|__|__|__|
</code></pre>
<p>现在大概知道不是 <code v-pre="">promise2 4 =&gt; promise1 4 =&gt; promise2 5 =&gt; promise1 5 =&gt; promise2 6 =&gt; promise1 6</code> 了<br>
但是为什么是 <code v-pre="">promise2 4 =&gt; promise2 5 =&gt; promise1 4 =&gt; promise2 6 =&gt; promise2 5 =&gt; promise1 6</code> ?<br>
问题在于 <code v-pre="">promise2 5</code> 在 <code v-pre="">promise1 4</code> 之前执行？</p>
<p>我们尝试加一下 性能测试 performance.now()，图又是什么样子的呢？</p>
<pre v-pre=""><code v-pre="" class="language-sh"><span class="hljs-comment"># promise1 2 1716.5999999997439</span>
<span class="hljs-comment"># promise2 2 1717.3000000038883</span>
<span class="hljs-comment"># promise1 3 1717.800000013085</span>
<span class="hljs-comment"># promise2 3 1718.1000000127824</span>
<span class="hljs-comment"># promise2 4 1718.5000000026776</span>
<span class="hljs-comment"># promise2 5 1718.9000000071246</span>
<span class="hljs-comment"># promise1 4 1719.200000006822</span>
<span class="hljs-comment"># promise2 6 1719.600000011269</span>
<span class="hljs-comment"># promise1 5 1720.0000000011642</span>
<span class="hljs-comment"># promise1 6 1720.3000000008615</span>
<span class="hljs-comment"># 很不幸，经过多次测试我们发现 promise1 4 一直介于 promise2 5、6之间，这个就回到事件循环本质上了，每一个 then 都是在微队列的一个切片上，不会重合，所以上面的图如下：</span>
2  3        4  5  6
|__|_____ __|__|__|

 2  3  4  5  6
 |__|__|__|__|
</code></pre>
<blockquote>
<p>看到这里我好像明白了，看来 promise 垫片需要重写了，因为返回 Promise 并不是让其代替默认返回的 Promise 而是照常返回，只是 value 是 Promise 实例
显然事件循环是在 resolve reject 内部实现</p>
</blockquote>
<details>
  <summary>js</summary>
<pre v-pre=""><code v-pre="" class="language-js"><span class="hljs-built_in">Promise</span>.resolve = <span class="hljs-function"><span class="hljs-keyword">function</span>(<span class="hljs-params">result</span>) </span>{
  <span class="hljs-keyword">return</span> <span class="hljs-keyword">new</span> <span class="hljs-built_in">Promise</span>(<span class="hljs-function"><span class="hljs-params">resolve</span> =&gt;</span> {
    <span class="hljs-keyword">if</span> (result &amp;&amp; result.constructor === <span class="hljs-built_in">Promise</span>) {
      result.then(resolve)
    } <span class="hljs-keyword">else</span> {
      resolve(result)
    }
  })
}

<span class="hljs-built_in">Promise</span>.prototype.then = <span class="hljs-function"><span class="hljs-keyword">function</span>(<span class="hljs-params">fn, fn2</span>) </span>{
  <span class="hljs-comment">// 省略状态判断</span>
  <span class="hljs-keyword">return</span> <span class="hljs-built_in">Promise</span>.resolve(fn(<span class="hljs-keyword">this</span>.data))
}

<span class="hljs-comment">// 可能还是懵的，那我们再来看 返回 Promise.resolve(res)即 result就是 Promise.resolve(res)</span>
<span class="hljs-built_in">Promise</span>.resolve = <span class="hljs-function"><span class="hljs-keyword">function</span>(<span class="hljs-params">result</span>) </span>{
  <span class="hljs-keyword">return</span> <span class="hljs-keyword">new</span> <span class="hljs-built_in">Promise</span>(<span class="hljs-function"><span class="hljs-params">resolve</span> =&gt;</span> {
    <span class="hljs-keyword">if</span> (result &amp;&amp; result.constructor === <span class="hljs-built_in">Promise</span>) {
      <span class="hljs-comment">// Promise.resolve(res).then(resolve)</span>
      <span class="hljs-keyword">let</span> pro = <span class="hljs-built_in">Promise</span>.resolve(res) <span class="hljs-comment">// Promise.resolve 内部调用一次 resolve</span>
      pro.then(fn) <span class="hljs-comment">// then 内部调用 Promise.resolve, Promise.resolve 调用 resolve 两次了</span>
      <span class="hljs-comment">// 这里 fn 就是 resolve 一共三次，所以间隔了三次事件循环</span>
    } <span class="hljs-keyword">else</span> {
      resolve(result)
    }
  })
}
</code></pre>
</details>
<p>现在终于能解释为什么 <code v-pre="">return Promise.resolve(res)</code> 间隔了三次事件循环了</p>
<h4 id="总结">总结</h4>
<p>then 方法内部实现看来比我想的简单，却存在坑了，因为原本一次事件循环搞定现在却只能通过三次，启发在于 catch 的使用</p>
<pre v-pre=""><code v-pre="" class="language-js"><span class="hljs-keyword">new</span> <span class="hljs-built_in">Promise</span>(<span class="hljs-function">(<span class="hljs-params">resolve, reject</span>) =&gt;</span> {
  reject(<span class="hljs-number">1</span>)
}).then(<span class="hljs-function"><span class="hljs-params">res</span> =&gt;</span> <span class="hljs-built_in">console</span>.log(res, <span class="hljs-string">'promise1Resolved'</span>))
.catch(<span class="hljs-function"><span class="hljs-params">err</span> =&gt;</span> <span class="hljs-built_in">console</span>.log(err, <span class="hljs-string">'promise1Rejected'</span>))

<span class="hljs-keyword">new</span> <span class="hljs-built_in">Promise</span>(<span class="hljs-function">(<span class="hljs-params">resolve, reject</span>) =&gt;</span> {
  reject(<span class="hljs-number">1</span>)
}).then(<span class="hljs-function"><span class="hljs-params">res</span> =&gt;</span> <span class="hljs-built_in">console</span>.log(res, <span class="hljs-string">'promise2Resolved'</span>), err =&gt; <span class="hljs-built_in">console</span>.log(err, <span class="hljs-string">'promise2Rejected'</span>))
</code></pre>
<p>显然是先 catch2</p>
<h3 id="关于 macrotask &amp; microtask">关于 macrotask &amp; microtask</h3>
<p>先祭出一张图吧<br>
<img src="../assets/imgs/eventLoop.jpg" width="500px"></p>
<h4 id="一次事件循环只执行一个 macrotask">一次事件循环只执行一个 macrotask</h4>
<p>看一个例子</p>
<pre v-pre=""><code v-pre="" class="language-js">setTimeout(<span class="hljs-function"><span class="hljs-params">()</span> =&gt;</span> {
  <span class="hljs-built_in">Promise</span>.resolve(<span class="hljs-number">2</span>)
    .then(<span class="hljs-built_in">console</span>.log)
}, <span class="hljs-number">0</span>)

setTimeout(<span class="hljs-function"><span class="hljs-params">()</span> =&gt;</span> {
  <span class="hljs-built_in">console</span>.log(<span class="hljs-number">1</span>)
}, <span class="hljs-number">0</span>)
</code></pre>
<p>答案是 2 1。或许你这里就算用了 performance 也不能看出端倪，但是我们知道 Promise 是异步就好了</p>
<pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-meta">&lt;!DOCTYPE html&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">html</span> <span class="hljs-attr">lang</span>=<span class="hljs-string">"en"</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">head</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">title</span>&gt;</span>test macrotask<span class="hljs-tag">&lt;/<span class="hljs-name">title</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">head</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">body</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"test"</span>&gt;</span>123<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
      <span class="hljs-keyword">let</span> test = <span class="hljs-built_in">document</span>.querySelector(<span class="hljs-string">'.test'</span>)
      setTimeout(<span class="hljs-function"><span class="hljs-params">()</span> =&gt;</span> {
        setTimeout(<span class="hljs-function"><span class="hljs-params">()</span> =&gt;</span> {
          test.style.width = <span class="hljs-string">'300px'</span>
          <span class="hljs-built_in">Promise</span>.resolve(performance.now())
            .then(<span class="hljs-built_in">console</span>.log)
        }, <span class="hljs-number">0</span>)
        setTimeout(<span class="hljs-function"><span class="hljs-params">()</span> =&gt;</span> {
          test.style.width = <span class="hljs-string">'200px'</span> <span class="hljs-comment">// 需要重排才行，如果一个引发重绘一个引发重排也不会 render</span>
          <span class="hljs-built_in">console</span>.log(performance.now(), <span class="hljs-string">'timer2'</span>)
        }, <span class="hljs-number">0</span>)
      }, <span class="hljs-number">300</span>) <span class="hljs-comment">// 需要 firstRender 完成</span>
    </span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">body</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">html</span>&gt;</span>
</code></pre>
<p>好吧，我承认为了把它们分到两次事件循环花了很长时间（更新了 Chrome 就不行了。。。。），因为浏览器实在太聪明了！！！<br>
首先完整的事件循环并不是 run 一个 macrotask 后立马 render 而是 判断是否需要重现渲染。这个完全就是看浏览器心情了。。。。<br>
根据上面的例子我们得到的结论就是 <strong>每次事件循环只执行一个 macrotask</strong> ，但是问题来了</p>
<ol>
<li>如果把前一个 time 改为 1 会先执行哪个？</li>
<li>我们在写动画时用 setTimeout 0 会发生什么？</li>
</ol>
<p>第一个问题的答案是不确定，完全看心情，笔者使用 chrmoe 基本是间隔 2 毫秒才会先执行第二个，但至少可以确定的是 不一定 根据 time（间隔比较小） 参数来决定 installTimer 顺序<br>
第二个问题 浏览器真的很聪明，我使用的 chrome 开始一般 合并 3-4 帧，然后 合并 10 多帧，最后基本是 7-8 帧，才渲染一次（不管是是否引发重排）</p>
<h4 id="一次事件循环会执行完所有微队列">一次事件循环会执行完所有微队列</h4>
<p>这一部分感觉没什么好说的，但是可以说的是必须清空微队列才会进入新的事件循环，也就是意味着在一个微队列里执行新的微队列会在一次事件循环里</p>
<p><b class="show-blog">true</b></p>
</section></template>

<script>
      let test = document.querySelector('.test')
      setTimeout(() => {
        setTimeout(() => {
          test.style.width = '300px'
          Promise.resolve(performance.now())
            .then(console.log)
        }, 0)
        setTimeout(() => {
          test.style.width = '200px' // 需要重排才行，如果一个引发重绘一个引发重排也不会 render
          console.log(performance.now(), 'timer2')
        }, 0)
      }, 300) // 需要 firstRender 完成
    </script>


